@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Open Sans', sans-serif;
}

body {
  margin: 0;
}

.slide-in-left-enter {
  transform: translateX(-100%);
}

.slide-in-left-enter-active {
  transform: translateX(0);
  opacity: 1;
  transition: all 200ms;
}

.slide-in-left-exit {
  transform: translateX(0%);
  opacity: 1;
}

.slide-in-left-exit-active {
  transform: translateX(-100%);
  opacity: 0;
  transition: all 200ms;
}

.center {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}
.main-header {
    width: 100%;
    height: 5rem;
    display: flex;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    background: lightgrey;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.26);
    padding: 0 1rem;
    z-index: 5;
    justify-content: space-between;
}



main {
    margin-top: 10rem;
}

@media (min-width: 768px) {
    .main-header p {
        display: flex;
    }
}

@media (max-width: 768px) {
    .main-header {
        height: 8rem;
    }
}


.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.75);
  z-index: 10;
}

.model {
  z-index: 100;
  position: fixed;
  top: 22vh;
  left: 10%;
  width: 80%;
  background: white;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 8px;
}

.model__header {
  width: 100%;
  padding: 1rem 0.5rem;
  background: gray;
  color: black;
}

.model__header h2 {
  margin: 0.5rem;
}

.model__content {
  padding: 1rem 0.5rem;
}

.model__footer {
  padding: 1rem 0.5rem;
}

@media (min-width: 768px) {
  .model {
    left: calc(50% - 20rem);
    width: 40rem;
  }
}

.model-enter {
  transform: translateY(-10rem);
  opacity: 0;
}

.model-enter-active {
  transform: translateY(0);
  opacity: 1;
  transition: all 200ms;
}

.model-exit {
  transform: translateY(0);
  opacity: 1;
}

.model-exit-active {
  transform: translateY(-10rem);
  opacity: 0;
  transition: all 200ms;
}

.button {
    font: inherit;
    padding: 0.5rem 1.5rem;
    border: 1px solid gray;
    border-radius: 4px;
    background: black;
    color: white;
    cursor: pointer;
    margin-right: 1rem;
    margin-bottom: 0.5rem;
    text-decoration: none;
    display: inline-block;
}

.button:focus {
  outline: none;
}

.button:hover,
.button:active {
  background: lightgray;
  border-color: lightgray;
}

.button--inverse {
  background: transparent;
  color: #ff0055;
}

.button--inverse:hover,
.button--inverse:active {
  color: white;
  background: #ff0055;
}

.button--danger {
  background: #830000;
  border-color: #830000;
}

.button--danger:hover,
.button--danger:active {
  background: #f34343;
  border-color: #f34343;
}

.button:disabled,
.button:hover:disabled,
.button:active:disabled {
  background: #ccc;
  color: #979797;
  border-color: #ccc;
  cursor: not-allowed;
}

.button--small {
  font-size: 0.8rem;
}

.button--big {
  font-size: 1.5rem;
}
.lds-dual-ring {
  display: inline-block;
  width: 64px;
  height: 64px;
}

.lds-dual-ring:after {
  content: ' ';
  display: block;
  width: 46px;
  height: 46px;
  margin: 1px;
  border-radius: 50%;
  border: 5px solid #510077;
  border-color: #510077 transparent #510077 transparent;
  -webkit-animation: lds-dual-ring 1.2s linear infinite;
          animation: lds-dual-ring 1.2s linear infinite;
}

.loading-spinner__overlay {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(255, 255, 255, 0.9);
  display: flex;
  justify-content: center;
  align-items: center;
}

@-webkit-keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.nav-list {
    list-style-type: none;
    display: block;
    margin: 0;
    padding: 0;
}

.nav-list li {
    background: #FFFF00;
    margin: auto;
    border: 2px solid black;
    width: 15%;
    margin-bottom: 30px;
    border-radius: 5px;
}

.nav-list a {
    border: 2px solid transparent;
    color: #292929;
    text-decoration: none;
    padding: 0.5rem;
}

.nav-list a:hover,
.nav-list a:active,
.nav-list a.active {
    background: grey;
    border-color: #292929;
    color: #292929;
}

